import { Component, OnInit } from '@angular/core';
import * as jsPDF from 'jspdf';
import { ActivatedRoute, Router } from "@angular/router";
import { PeticionesService } from '../../../services/peticiones.service';


@Component({
  selector: 'app-print-certificate',
  templateUrl: './print-certificate.component.html',
  styleUrls: ['./print-certificate.component.css'],
  providers: [PeticionesService]
  
})
export class PrintCertificateComponent implements OnInit {

  public coaching='data:image/png;base64,';
  public escudo='data:image/png;base64,';
  public logoCecap='data:image/jpeg;base64,';

  public imagenEvento=null;
 
  public logo1:string;
 public logo2:string;
 public doc =new jsPDF('landscape');



 public eventId;
 public event;
//  public inscriptions;
 public lista_personasPorModulo;
//  public moduleId;
 public moduleName;
 public certificateData;
 public modularForAssist;
 public nombreFechaCertificado;
 public nombreFechaDepartamento;
 public nombreEventoModulo;


 public personId;
 public moduleId;
 public profileId;
public person;
 

 constructor(
  private route: ActivatedRoute,
  private router: Router,
  private _peticionesService: PeticionesService
 ) { }
 
 ngOnInit(){
  this.route.params.subscribe(params => {
  this.personId = params.id.split("-")[0];
  this.profileId=params.id.split("-")[1];
  this.moduleId=params.id.split("-")[2];

  this._peticionesService.getPerson(this.personId).subscribe(res=>{
    this.person=res;
  })

  });
}

onSubmit(){


  this.printCertificates();
}
cancel(){
  window.history.back();
}

printCertificates(){

  

  
    this.oneCertificate(this.person.first_name+' '+this.person.last_name);
    
 

  this.doc.save('Test.pdf');
   let personProfileModule={} as PersonProfileModule;
            personProfileModule.personId = this.personId;
            personProfileModule.profileId = this.profileId;
            personProfileModule.moduleId = this.moduleId;
            
    this._peticionesService.markPrintCertificate(personProfileModule).subscribe(response=>{
      var res = response
    });
  window.history.back();
  
  
}


onSelectFileEscu(event) { // called each time file input changes
    if (event.target.files && event.target.files[0]) {
      var reader:any;
       reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]); // read file as data url

      reader.onload = (event) => { // called once readAsDataURL is completed
        this.escudo = event.target.result;
        
      }
    }
}
onSelectFileLogo1(event) { // called each time file input changes
  if (event.target.files && event.target.files[0]) {
    var reader :any;
    reader = new FileReader();

    reader.readAsDataURL(event.target.files[0]); // read file as data url

    reader.onload = (event) => { // called once readAsDataURL is completed
      this.logoCecap= event.target.result;
      
    }
  }
}
onSelectFileLogo2(event) { // called each time file input changes
  if (event.target.files && event.target.files[0]) {
    var reader :any;
    reader = new FileReader();

    reader.readAsDataURL(event.target.files[0]); // read file as data url

    reader.onload = (event) => { // called once readAsDataURL is completed
      this.coaching= event.target.result;
      
    }
  }
}





oneCertificate(namePerson:string){

  /////////////////lineas/////////////
      this.doc.setLineWidth(3);
      this.doc.setDrawColor(0,179,60);
      this.doc.line(10, 10, 10, 190);

      this.doc.setLineWidth(3);
      this.doc.setDrawColor(179, 179, 179);
      this.doc.line(16, 15, 16, 195);

      this.doc.setLineWidth(3);
      this.doc.setDrawColor(0,179,60);
      this.doc.line(287, 10, 287, 190);

      this.doc.setLineWidth(3);
      this.doc.setDrawColor(179, 179, 179);
      this.doc.line(281, 15, 281, 195);
///////////////////////////////////////////////
    
      this.doc.addImage(this.escudo, 'JPEG', 245,40, 30,27 );
      this.doc.addImage(this.coaching, 'JPEG', 225, 8, 56,28);
      this.doc.addImage(this.logoCecap, 'JPEG', 20, 11, 35,35);
      if(this.imagenEvento!=null){
        this.doc.addImage(this.imagenEvento, 'JPEG', 20, 48, 35,35);

      }
      
      
      
      
      

      this.doc.setFont("times");
      this.doc.setFontType("bold");
      this.doc.setFontSize(13);
      this.doc.setTextColor(0,179,60);
      this.doc.text(150, 20, 'El CENTRO DE CAPACITACION Y ACTUALIZACION PROFESIONAL',null,null,'center');

      this.doc.setFont("times");
      this.doc.setFontType("italic");
      this.doc.setFontSize(13);
      this.doc.setTextColor(0,179,60);
      this.doc.text(150, 28  , '"Lideres en Consultoria"',null,null,'center');

      this.doc.setFont("times");
      this.doc.setFontType("bold");
      this.doc.setFontSize(18);
      this.doc.setTextColor(0,0,0);
      this.doc.text(150, 65, 'C E R T I F I C A D O  A L  P A R T I C I P A N T E',null,null,'center');

      this.doc.text(150, 70, '____________________________________________________',null,null,'center');



      this.doc.setFont("times");
      this.doc.setFontType("italic");
      this.doc.setFontSize(34);
      this.doc.text(150,100,namePerson,null,null,"center");
      // this.doc.text(150, 130, 'This is centred text.', null, null, 'center');
      this.doc.text(150,107,'____________________________',null,null,'center');

      this.doc.setFont("times");
      this.doc.setFontType("normal");
      this.doc.setFontSize(14);
      this.doc.text(150,116,'Por haber concluidoel Entrenamiento Superior VIP: ',null,null,'center')


      this.doc.setFont("times");
      this.doc.setFontType("bold");
      this.doc.setFontSize(14);
      this.doc.text(150,122,'"'+this.nombreEventoModulo+'", ',null,null,'center');

      this.doc.setFont("times");
      this.doc.setFontType("normal");
      this.doc.setFontSize(14);
      this.doc.text(150,128,'presentado por: ',null,null,'center');

      this.doc.setFont("times");
      this.doc.setFontType("italic");
      this.doc.setFontSize(14);
      this.doc.text(150,134,'CECAP CONSULTING',null,null,'center');

      this.doc.setFont("times");
      this.doc.setFontType("normal");
      this.doc.setFontSize(14);
      this.doc.text(150,140,this.nombreFechaCertificado,null,null,'center');

      this.doc.setFont("times");
      this.doc.setFontType("normal");
      this.doc.setFontSize(14);
      this.doc.text(265,165,this.nombreFechaDepartamento,null,null,'right');


      // this.doc.addFont('ComicSansMS', 'Comic Sans', 'normal');
      // this.doc.setFont('Comic Sans');
      // this.doc.text(50,50,'Hello World');

      // this.doc.addFont('asdf', 'asdf', 'normal');
      // this.doc.setFont('asdf');
      // this.doc.text(50,80,'Hello World');

      // this.doc.setFont("monotpe");
      // this.doc.setFontType("normal");
      // this.doc.setFontSize(14);
      // this.doc.text(265,175,'Cochabamba - Bolivia,2017 ',null,null,'right');

      // console.log(this.doc.getFontList());

      


}


  
}


export interface PersonItem{

  persons:string,
  name:string,
  ci:number,
  cellphone:number,
  canceled_price:number,
  price_event:number,
  assist:boolean
}
export interface PersonEventModule{

  personId:string,
  eventId:string,
  moduleId:string,
}
export interface PersonProfileModule{
  personId:string,
  profileId:string,
  moduleId:string,
}


